@import url('https://fonts.googleapis.com/css?family=Nunito'); 


body {
  background-color: var(--color-black);
  font-family: 'Nunito', sans-serif;
  margin: 0;
  color: var(--color-white);
}

:root {
    --font-family: "Nunito";
    --color-white: #FFFFFE;
    --color-black: #16161A;
    --color-primary: #7F5AF0;
    --color-secundary: #A786DF;
    --color-tertiary: #72757E;
    --color-green: #2CB67D;
    --color-gray: #94A1B2;
    --color-purple: #6B47DC;
    --color-neutral: #232E35;
    --color-red: #EF4565;
}


  .container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  } 
  .checkmark {
    position: absolute;
    right: 8px;
    height: 25px;
    width: 25px;
    background-image:url("https://gejocad.github.io/DailyBits/img/radio-button.png");
    border-radius: 50%;
  }



  /* When the radio button is checked, add a blue background */
  .container input:checked ~ .checkmark {
    background-image:url("https://gejocad.github.io/DailyBits/img/radio-button-active.png");
  }

  /* Create the indicator (the dot/circle - hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the indicator (dot/circle) when checked */
  .container input:checked ~ .checkmark:after {
    background-image:url("https://gejocad.github.io/DailyBits/img/radio-button-active.png");
  }
